      ],
      answers: ["", "", "", ""],
      photoPlaceholder: require("@/assets/images/activities/upload.jpg")
    };
  },
  methods: {
    onFileChange(e, id) {
      const file = e.target.files[0];
      const reader = new FileReader();
      this.photos[id].file = file;
      reader.onload = e => {
        this.photos[id].source = e.target.result;
      };
      reader.readAsDataURL(file);
    }
  }
};
